              bill: "لیست قبض‌ها / فاکتورها",
            },
            pageDescriptionList: {
              current: "در این بخش می‌توانید لیست شارژهای جاری ثبت شده قبلی را ببینید. با کلیک کردن بر روی بدهی واحد، امکان حذف یا ویرایش وجود دارد.",
