                return `بدهی ${this.selectedDebt.debt_name} ${this.selectedDebt.cycle_name} برای همه واحدها حذف خواهد شد. آیا اطمینان دارید؟`
            }
        },
        filteredItems() {
            const texts = []

            this.cycleDetail.forEach(item => {
                if (this.selectedOption.map(selectedItem => selectedItem.targetValue)
                    .includes(item.value)) {
                    texts.push(item.text)
                }
            })

            if (texts.length > 0) {
                return this.pageData.debtor_list?.list.filter(item => texts.includes(
                  this.type === 'bill' ? item.debt_category_name : item.cycle_name
                ))
            }

            return this.pageData.debtor_list?.list
        }
    },
    async created() {
        try {
            if (this.$route.query.type) {
              this.type = this.$route.query.type
            }
            this.typeName = this.typeNameList[this.type]
            this.loading = true;
            const { data } = await axios.get("manager/debt/formula?type=" + this.type, {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem('token')}`,
                },
            });
            this.mainList = data.debtor_list.list
            // load filter options
            if (this.type === 'bill') {
              const { data: data } = await axios.get(
                  "/manager/common/bill", {
                      headers: {
                          Authorization: `Bearer ${localStorage.getItem('token')}`,
                      },
                  }
              );
              this.cycleDetail = Object.keys(data.list).map((key) => ({
                  text: data.list[key],
                  value: key,
              }));

            } else {
              // load cycle details
              const { data: CycleDetail } = await axios.get(
                  "/manager/common/cycle-detail", {
                      headers: {
                          Authorization: `Bearer ${localStorage.getItem('token')}`,
                      },
                  }
              );
              this.cycleDetail = CycleDetail.list.map((item) => ({
                  debtList: [],
                  text: item.cycle_name,
                  value: item.cycle_detail_id,
              }));
            }

            this.pageData = data;
            this.loading = false;

            // console.log(data);
        } catch (err) {
            console.log(err);
        }
    },
    methods: {
        separate() {
            this.debtTemp = separator(this.debtTemp)
        },
        generateBgForBox,
        randomVariantGenerator,
        randomColorByIndex,
        randomBgColorByIndex,
        deleteAllCharge() {},
        async chargeRegister() {
            if (this.type === 'bill') {
              this.$router.push({ path: '/bill-debt-registration/registry' })
              return
            }

            const { data: statusData } = await axios.get("manager/debt/formula/status", {
                params: {
                    type: this.type,
                },
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`
                }
            });
            this.buttonDetails = statusData.debtor_list;
            if (this.buttonDetails.length && this.buttonDetails[0].text === 'ثبت فرمول') {
                this.$bvModal.show('formula-register')
            } else {
                this.$router.push({ path: '/debt-registration/' + this.type + '/store' })
            }

            //     api call for formula if no modal else go to /debt-registration/current/store
        },
        async onCollapseToggle(selected, force = false) {
            this.collapsedItem = selected
            if (selected.debtList !== undefined && !force) return;
            this.showLoading = true
            const data = {
                cycle_detail_id: selected.cycle_detail_id,
                debt_category_id: selected.debt_category_id,
                debt_start: selected.debt_start,
                type: this.type,
            }
            userList.formulaCharge(data)
                .then(res => {
                    selected.debtList = []
                    // eslint-disable-next-line no-param-reassign
                    // ### main list
                    this.collapsedItem.locale_debt_fee = res.data.debtor_list.debt_sum
                    this.collapsedItem.remain = res.data.debtor_list.remain_sum
                    selected.debtList = res.data.debtor_list.list.map(v => ({
                        ...v,
                        isEditing: false,
                        isDeleting: false,
                        detailList: null,
                        showArrow: false,
                    }))
                    this.showLoading = false
                    this.$forceUpdate()
                })
        },

        async onUnitToggle(unit, type) {
            if (unit.detailList !== null && type !== 'refresh') return;
            const detaildata = { unit_debt_id: unit.unit_debt_id }
            this.showLoading = true
            userList.debtDetailList(detaildata)
                .then(res => {
                    unit.detailList = res.data.list
                    unit.debt_fee = res.data.debt_fee
                    unit.remain_sum = res.data.remain_sum
                    this.showLoading = false
                })
        },
        async ContinueDeletingAll() {
            // delete
            this.deleteAllPending = true
            const data = {
                cycle_detail_id: this.selectedDebt.cycle_detail_id,
                debt_category_id: this.selectedDebt.debt_category_id,
                debt_start: this.selectedDebt.debt_start,
                type: this.type,
            }
            await managerService.bulkDelete(data)
                .then(res => {
                    this.deleteAllPending = false
                    this.showDeleteAllModal = false
                    this.$toast({
                        component: ToastificationContent,
                        props: {
                            title: res.data.message,
                            icon: 'EditIcon',
                            variant: 'success',
                        }
                    })
                })
            await this.reloadMainList()
            // eslint-disable-next-line radix
        },
        async reloadMainList() {
            const { data } = await axios.get("manager/debt/formula?type=" + this.type, {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem('token')}`,
                },
            });
            this.pageData = data;
            // console.log(data)
        },
        async ContinueEditing(unit) {
            // eslint-disable-next-line radix
            this.editPending = true
            // eslint-disable-next-line radix, no-param-reassign
            unit.debt_fee = this.debtTemp
            // eslint-disable-next-line radix
            const updatedata = {
                unit_debt_id: unit.unit_debt_id,
                debt_fee: parseInt(unit.debt_fee.replaceAll('٬', ''))
            }
            await userList.debtUpdate(updatedata)
                .then(res => {
                    this.onUnitToggle(unit, 'refresh')
                    const data = {
                        cycle_detail_id: this.collapsedItem.cycle_detail_id,
                        debt_category_id: this.collapsedItem.debt_category_id,
                        debt_start: this.collapsedItem.debt_start,
                        type: this.type,
                    }
                    userList.formulaCharge(data)
                        .then(res => {
                            // ### main list
                            this.collapsedItem.locale_debt_fee = res.data.debtor_list.debt_sum
                            this.collapsedItem.remain = res.data.debtor_list.remain_sum
                        })
                    this.$toast({
                        component: ToastificationContent,
                        props: {
                            title: res.data.message,
                            icon: 'EditIcon',
                            variant: 'success',
                        }
                    })
                    this.editPending = false
                    this.showEditModal = false
                    // eslint-disable-next-line no-param-reassign
                    unit.isEditing = false

                    // this.form.isEditing = false
                })
        },
        async ContinueRefresh(selected) {
            this.refreshPending = true
            const refreshdata = { unit_debt_id: selected.unit_debt_id }
            await userList.debtRefresh(refreshdata)
                .then(res => {
                    this.onUnitToggle(selected, 'refresh')
                    const data = {
                        cycle_detail_id: this.collapsedItem.cycle_detail_id,
                        debt_category_id: this.collapsedItem.debt_category_id,
                        debt_start: this.collapsedItem.debt_start,
                        type: this.type,
                    }
                    userList.formulaCharge(data)
                        .then(res => {
                            // ### main list
                            this.collapsedItem.locale_debt_fee = res.data.debtor_list.debt_sum
                            this.collapsedItem.remain = res.data.debtor_list.remain_sum
                        })
                    this.$toast({
                        component: ToastificationContent,
                        props: {
                            title: res.data.message,
                            icon: 'EditIcon',
                            variant: 'success',
                        }
                    })
                    this.showRefreshModal = false
                    this.refreshPending = false
                })
        },
        async ContinueDeleting(selected) {
            this.deletePending = true
            const deletedata = { unit_debt_id: selected.unit_debt_id }
            await userList.debtDelete(deletedata)
                .then(res => {
                    this.onCollapseToggle(this.collapsedItem, true)
                    this.$toast({
                        component: ToastificationContent,
                        props: {
                            title: res.data.message,
                            icon: 'EditIcon',
                            variant: 'success',
                        }
                    })
                    this.deletePending = false
                    this.showDeleteModal = false
                    // selected.debtList = selected.debtList.filter(item => item.unit_debt_id != selected.unit_debt_id)
                })
        },
        showDeleteAllModalfunc(item) {
            this.selectedDebt = item
            this.showDeleteAllModal = true
        },
        showDeleteModalfunc(selected) {
            this.showDeleteModal = true
            this.selected = selected
            this.selected.isEditing = false
        },
        showEditModalfunc(selected) {
            this.showEditModal = true
            this.selected = selected
            this.selected.isEditing = false
        },
        showRefreshModalfunc(selected) {
            this.showRefreshModal = true
            this.selected = selected
            this.selected.isEditing = false
        },
        numberFormat(value) {
            return value.replace(/,/g, '')
                .split('')
                .join(',')
        },
        onEditDebt(selected) {
            // eslint-disable-next-line no-param-reassign
            selected.isEditing = !selected.isEditing;
            this.debtTemp = separator(selected.debt_fee);
            this.$forceUpdate();
        }
    }
};
</script>
